import { Typography, Tabs } from "antd";
import React, { useState } from "react";

const App = () => {
  const [category] = useState([
    {
      name: "所有",
    },
    {
      name: "特價",
    },
    {
      name: "最新",
    },
  ]);

  return (
    <>
      <Tabs
        centered
        defaultActiveKey="1"
        items={category.map((x, index) => {
          return {
            label: <Typography.Title level={5}>{x.name}</Typography.Title>,
            key: index,
            children: ``,
          };
        })}
      />
    </>
  );
};

export default App;
